.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 22px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .container {
    font-size: 16px;
  }
  .title {
    font-size: 25px;
  }
}
