.container {
  /* background-color: #63c5bd; */
  background-color: var(--color-1);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 35px;
  color: #fff;
  position: relative;
  z-index: 2;
  letter-spacing: 1px;
  line-height: 1.2;
}

.girlImg {
  position: absolute;
  right: 40px;
  top: 20px;
  max-height: 100%;
}
.content_block {
  z-index: 2;
  position: relative;
  max-width: 75%;
}
.title {
  font-size: clamp(1.2rem, 5.4vw, 4rem);

  font-weight: 600;
}
.subTitle {
  font-size: clamp(1rem, 4vw, 2.5rem);

  margin-bottom: 20px;
}

.text {
  font-size: clamp(0.9rem, 2vw, 1.8rem);

  max-width: 70%;
  margin-bottom: 20px;
}
.text_start {
  font-size: clamp(1rem, 1.5vw, 1.8rem);

  max-width: 80%;
  margin-bottom: 5px;
  color: var(--color-5);
  font-weight: 600;
}
.start_group_btn {
  background-color: var(--color-4);
  padding: 14px;
  text-transform: uppercase;
  border-radius: 10px;
  display: inline-block;
  margin-top: 20px;
  /* font-weight: 600; */
  letter-spacing: 2px;
  z-index: 5;
  position: relative;
  margin-bottom: 20px;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
  .girlImg {
    right: 0px;
    max-height: 100%;
  }
}

@media (max-width: 768px) {
  .girlImg {
    right: -20px;
  }
}

@media (max-width: 576px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
  .girlImg {
    right: -60px;
    max-height: 90%;
  }
}

@media (max-width: 480px) {
  .content_block {
    max-width: 70%;
  }
  .girlImg {
    right: -55px;
    max-height: 70vw;
  }
  .text {
    max-width: 78%;
  }
}
@media (max-width: 380px) {
  .text {
    max-width: 90%;
  }
}
