@import url(./classCss.css);
@import url(./reset.css);
@import url(./root.css);

/* ----------------- */

#app {
  height: 100%;
}
html,
body {
  position: relative;
  min-height: 100%;
  background-color: var(--color-6);
}

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  background-image: url('../assets/img/pattern.png');
}

/* Стилізація скрола  body*/

body::-webkit-scrollbar {
  width: 12px; /* ширина всей полосы прокрутки */
}

body::-webkit-scrollbar-track {
  /* цвет зоны отслеживания */
  /* background: orange; */
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  /* цвет бегунка */
  /* background-color: blue; */
  background-color: var(--color-1);

  border-radius: 20px; /* округлось бегунка */

  /* отступ вокруг бегунка */
  /* border: 3px solid orange;  */
  border: 3px solid var(--color-2);
}

/* / Стилізація скрола  body*/

.adaptiveImage {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
}
.adaptiveImage img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover !important;
  /* object-fit: contain; */
}
.imgAdaptive {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* object-fit: contain; */
}

/* ---------------------------------------------- */

.site_wrapper {
  background-color: #fff;
  background-image: url('../assets/img/pattern.png');
  max-width: 1100px;
  margin: 0 auto;
  overflow-x: hidden;
}
.container_top {
  background-position-x: left;
  background-position-y: bottom;
  background-image: url('../assets/img/pattern.png');
  background-color: #fff;
  z-index: 100;
  max-width: 1100px;
  margin: 0 auto;
  position: sticky;
  top: 0;
}
.img-decor {
  opacity: 0.92;
  margin: 0 auto;
}
