.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* ---------------------------------------- */
.video_container {
  position: relative;
  width: 100%;
  height: 80vh;
  max-height: 1000px;
  aspect-ratio: 16 / 9;
  background: #000;
  /* background-color: var(--color-6); */

  background-image: url('../../assets/img/pattern.png');
  border-radius: 12px;
  overflow: hidden;
}

.video_container video {
  margin: 0 auto;
  max-width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}
/* ---------------------------------------- */

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 25px;
  }
}
