.container {
  background-color: var(--color-1);
  min-height: 500px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 50px;
  color: #fff;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 20px;
}
.content_wrapper {
  display: flex;
  gap: 30px;
}
.myPhoto {
  max-width: 45%;
  align-self: flex-start;
}
.text {
  align-self: center;
  font-size: 18px;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
  .content_wrapper {
    gap: 20px;
  }
  .title {
    font-size: 25px;
  }
  .text {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .content_wrapper {
    display: block;
  }
  .myPhoto {
    max-width: 100%;
    margin-bottom: 30px;
  }
  .text {
    font-size: 16px;
  }
}
