/* -------------------------- */
.top_block {
  display: flex;
  justify-content: space-between;
  background-color: var(--color-1);

  padding-left: 40px;
  padding-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.contacts_wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}
.call_btn {
  font-size: 22px;
  line-height: 1;
  font-weight: 600;
  color: var(--color-3);
  text-decoration: underline;
  margin-left: 15px;
}

/* -------------------------- */
@media (max-width: 992px) {
  .top_block {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 576px) {
  .top_block {
    padding-left: 15px;
    padding-right: 15px;
  }
  .call_btn {
    font-size: 18px;
    margin-left: 5px;
  }
}
